.elemsnr-admin .elemsnr-fields-container {
  -webkit-box-align: start;
  -webkit-align-items: flex-start;
  -ms-flex-align: start;
  align-items: flex-start;
  background-color: #ffffff;
  border: 1px solid #8c8f94;
  border-radius: 4px;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  gap: 5px;
  -webkit-box-pack: start;
  -webkit-justify-content: flex-start;
  -ms-flex-pack: start;
  justify-content: flex-start;
  margin: 0;
  margin-bottom: 20px;
  max-height: 480px;
  overflow-y: scroll;
  padding: 10px 15px;
  position: relative;
  width: 100%;
}

.elemsnr-admin .elemsnr-fields-container p {
  -webkit-box-flex: 0;
  -webkit-flex: 0 0 calc(33% - 20px);
  -ms-flex: 0 0 calc(33% - 20px);
  flex: 0 0 calc(33% - 20px);
  font-size: 13px;
  margin: 0 !important;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.elemsnr-admin .elemsnr-fields-container label + strong {
  margin-left: 4px;
}

.elemsnr-admin .elemsnr-fields-container label[for=elemsnr-toggle-defaults],
.elemsnr-admin .elemsnr-fields-container label[for=elemsnr-toggle-widgets] {
  background-color: rgba(224, 225, 227, 0.85);
  cursor: pointer;
  display: inline-block;
  font-size: 10px;
  font-weight: 700;
  letter-spacing: 0.5px;
  padding: 8px;
  position: absolute;
  right: 0;
  text-transform: uppercase;
  top: 0;
}

.elemsnr-admin .elemsnr-fields-container input[onclick="return false;"] + label:after {
  content: " (required)";
  font-weight: 500;
}

.elemsnr-admin .elemsnr-container .elemsnr-button-group {
  border: 1px solid #2271b1;
  border-radius: 4px;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  display: inline-block;
  gap: 0;
  margin: 15px 0 !important;
}

.elemsnr-admin .elemsnr-container .elemsnr-button-group .button {
  border: 0;
  border-color: transparent;
  border-radius: 0;
  -webkit-box-shadow: none;
  box-shadow: none;
}

.elemsnr-admin .elemsnr-container .elemsnr-start-over-button {
  display: none;
}

.elemsnr-admin .elemsnr-container .elemsnr-toggle-media-modal {
  cursor: pointer;
}

.elemsnr-admin .elemsnr-container .elemsnr-change-posts-pages {
  left: 325px;
  position: absolute;
  top: 10px;
}

.elemsnr-admin .elemsnr-filters-bar {
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  background-color: rgba(224, 225, 227, 0.5);
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
  -ms-flex-pack: justify;
  justify-content: space-between;
  padding: 10px 15px;
  position: absolute;
  right: 0;
  top: 0;
  width: calc(100% - 20px);
}

.elemsnr-admin .elemsnr-filters-bar input[name=elemsnr-search-posts] {
  border: 0;
  font-size: 12px;
  max-width: 75%;
  width: 240px;
}

.elemsnr-admin .elemsnr-filters-bar label[for=elemsnr-toggle-posts] {
  cursor: pointer;
  display: inline-block;
  font-size: 12px;
  width: auto;
}

.elemsnr-admin .elemsnr-scroll-container {
  background-color: #ffffff;
  border: 1px solid #8c8f94;
  border-radius: 4px;
  display: none;
  height: 315px;
  margin: 15px 0;
  max-width: 800px;
  overflow-y: scroll;
  padding: 60px 15px 15px 15px;
  position: relative;
}

.elemsnr-admin .elemsnr-scroll-container p {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  font-size: 12px;
}

.elemsnr-admin .elemsnr-scroll-container p input[type=checkbox] {
  -webkit-box-flex: 0;
  -webkit-flex: 0 0 10px;
  -ms-flex: 0 0 10px;
  flex: 0 0 10px;
}

.elemsnr-admin .elemsnr-scroll-container p label {
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  cursor: pointer;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
  -ms-flex-pack: justify;
  justify-content: space-between;
  width: calc(100% - 40px);
}